@import "variables";
@import "components/chart";
@import "containers/stats";
@import "elements/buttons";

#iawp {
    box-sizing: border-box;

    * {
        box-sizing: border-box;
    }
    .postbox-header .iawp-button {
        text-decoration: none;
        padding: 6px 8px;
    }
    .inside {
        margin-top: 0;
        padding: 0;
    }
    .chart-container {
        padding: 6px 0 0;
    }
    .chart-inner {
        border: none;
        padding: 6px 12px 12px;
    }
    .legend-container {
        margin-bottom: 10px;
    }
    .legend-item {
        margin-left: 8px;

        &:first-child {
            margin-left: 0;
        }
        p {
            font-size: 11px;
        }
        span {
            width: 13px;
            height: 13px;
        }
    }
    .legend-title {
        display: none;
    }
    .quick-stats {
        display: flex;
        margin: 0;
        gap: 0;

        .stat {
            width: 50%;
            padding: 12px 18px;
            border: none;
            border-top: 1px solid #dedae6;
            border-radius: 0;

            &:first-child {
                border-right: 1px solid #dedae6;
            }
            .metric {
                font-size: 14px;
            }
            .percentage {
                font-size: 12px;
                padding: 1px 8px 1px 2px;
            }
            .values {
                display: flex;
                flex-wrap: wrap;
                margin: 0;
            }
            .count {
                font-size: 24px;
                word-break: break-word;
            }
            .period-label {
                font-size: 13px;
            }
        }
    }
}